.home-page-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.image-container{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

#zoomed-in-div {
    z-index: 999;
    position: relative;
    top: -16em;
    left: 21.5em;
  }
  

#desk-image{
  position: relative;
  z-index: 1;
  top: -8em;
  left: 35em;
}

#earth{
  position: relative;
  z-index: 0;
  top: 19em;
  left: -10em;
}

img{
  -khtml-user-select: none;
  -o-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.text{
  color: aliceblue;
  font-family: "Minecraft";
  margin: 2%;
  font-size: 50px;
  display: flex;
  flex-wrap: wrap;

  
}

