body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  /* background-color: var(--b-primary); */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "minecraft";
  src: url(/Portfolio/static/media/Minecraft.d7b98c45.ttf);
}


:root{
  font-family: 'Open Sans';
  --t-primary:rgb(245, 237, 237);
  --b-primary:rgb(0,0,0);
  --b-secondary:#051427;
  --b--tertiary:#530f1e;
  --b-quad:#a44322;
}

.about{
    background-color: black;
}

.about-fullscreen {
    background-color: black;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 999;
}
  
.title-about{
    font-size: 2rem;
    display: flex;
    flex-direction: flex-start;
    margin-top: 0;
    padding-top: 2rem;
    margin-left: 1rem;
    color: white;
}

.project{
    background-color: rgb(0, 0, 0);
}

.project-fullscreen {
    background-color: rgb(0, 0, 0);
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 999;
  }

.title-project{
    font-size: 2rem;
    display: flex;
    flex-direction: flex-start;
    margin-top:0;
    padding-top: 2rem;
    margin-left: 1rem;
    color: white;
}
.main-container-project{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px;
    margin: 20px;
}
.project-item{
    list-style: none;
    flex: 1 1;
    min-width: 20em;
    max-width: 20em;
    height: 20em;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 20px;
    outline: 1px solid rgb(255, 255, 255);
    border-radius: 30px;
    margin: 20px;
    box-shadow: 0px 20px 30px rgb(138, 8, 170), 0px 20px 35px  rgb(138, 8, 170);
}
.home-page-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.image-container{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

#zoomed-in-div {
    z-index: 999;
    position: relative;
    top: -16em;
    left: 21.5em;
  }
  

#desk-image{
  position: relative;
  z-index: 1;
  top: -8em;
  left: 35em;
}

#earth{
  position: relative;
  z-index: 0;
  top: 19em;
  left: -10em;
}

img{
  -o-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.text{
  color: aliceblue;
  font-family: "Minecraft";
  margin: 2%;
  font-size: 50px;
  display: flex;
  flex-wrap: wrap;

  
}


.home-page-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  
#tsparticles {
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
.nav-bar {
    background: rgb(119, 100, 100);
    border-radius: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1%;
  }
  
  .nav-ul {
    font-family: 'Minecraft';
    font-size: 2rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    list-style: none;
    grid-gap: 1rem;
    gap: 1rem;
    padding: 1%;
    margin: 0;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  .nav-li{
    position: relative;
  }
  
  .nav-link {
    text-decoration: none;
    color: azure;
    font-weight: 400;
    margin-right: 1rem;
  }
  .nav-link:visited {
    color: azure;
  }
.footer-ul {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 50px;
    background-color:black;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
  }



  


  

